import { combineReducers, applyMiddleware, compose } from 'redux';
import type {
  StoreEnhancer,
  StoreEnhancerStoreCreator,
  AnyAction,
  Reducer,
  PreloadedState,
  Store,
} from 'redux';
import {
  STORAGE,
  ReducersMap,
  Action,
  CreateSliceOptions,
  Return,
} from './types';
import { createSliceFactory } from './createSliceFactory';
import { createUseReducer } from './createEachUseReducer';

export function createDuraEnhancer(): StoreEnhancer<{
  createSlice: <S, A extends Action, M extends ReducersMap<S, A>>(
    options: CreateSliceOptions<S, A, M>,
  ) => Return<S, M>;
}> {
  const storage: STORAGE = {
    current: {
      reducerMap: {
        D: (state = {}) => state,
      },
    },
  };
  return function duraEnhancer(createStore) {
    return function (reducer, preloadedState) {
      const reduxStore = createStore(
        combineReducers({ ...storage.current.reducerMap, ...reducer }),
        preloadedState,
      );
      const createSlice = createSliceFactory(reduxStore, storage);
      return {
        ...reduxStore,
        createSlice,
      };
    };
  } as any;
}

